  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 767px) {
  .header__container {
    justify-content: flex-start;
  }
}

.logo {
  color: #111321;
  font-family: Manrope, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 32.78px;
}

@media screen and (min-width: 768px) {
  .logo {
    font-size: 32px;
    line-height: 43.71px;
  }
}

@media screen and (min-width: 1281px) {
  .logo {
    font-size: 42px;
    line-height: 57px;
  }
}

.navbar__items {
  display: none;
}

@media screen and (min-width: 768px) {
  .navbar__items {
    gap: 32px;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .navigation-menu__list {
    align-items: center;
    gap: 44px;
    margin-left: 48px;
    display: flex;
  }
}

@media screen and (min-width: 1281px) {
  .navigation-menu__list {
    gap: 69px;
    margin-left: 102px;
  }
}

.navigation-menu__link {
  color: #111321;
  letter-spacing: .02em;
  text-transform: none;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.172;
  text-decoration: none;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .navigation-menu__link {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.38;
  }
}

@media screen and (min-width: 1281px) {
  .navigation-menu__link {
    font-size: 18px;
  }
}

@media (min-width: 768px) {
  .navigation-menu {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .navbar__items--right {
    align-items: center;
    gap: 24px;
    display: flex;
  }
}

.button-burger {
  cursor: pointer;
  fill: #111321;
  background-color: #fff;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

@media (min-width: 768px) {
  .button-burger {
    display: none;
  }
}

.top-wrapper {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
}

.button-close {
  cursor: pointer;
  fill: #111321;
  background-color: #fff;
  border: none;
  outline: none;
  padding: 0;
  display: flex;
}

.mobile-container {
  height: 100%;
  width: 100%;
  z-index: 999;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: #fff;
  transition: all .5s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.mobile-container.is-open {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
  transition: all .5s ease-in-out;
  transform: translateY(0);
}

.mobile-container__wrap {
  max-width: 480px;
  min-height: 100%;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

@media (min-width: 480px) {
  .mobile-container__wrap {
    max-width: 100%;
  }
}

.mobile-menu__list {
  flex-direction: column;
  gap: 24px;
  margin-top: 68px;
  display: flex;
}

.mobile-menu__link {
  letter-spacing: .02em;
  color: inherit;
  align-items: center;
  gap: 14px;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 16px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
  text-decoration: none;
  display: flex;
}

.menu-container__icon {
  border: 1px solid #f4f4f4;
  border-radius: 50%;
  display: flex;
}

.current-page__icon {
  fill: #f4f4f4;
  position: absolute;
  right: 16px;
}

.current__page {
  pointer-events: none;
  cursor: default;
  position: relative;
}

@media screen and (min-width: 767px) {
  .current__page:after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #4440f6;
    border-radius: 2px;
    display: flex;
    position: absolute;
    bottom: -12px;
  }
}

.current__page--mob {
  color: #f4f4f4;
  background-color: #4440f6;
}

.toggle__wrapper {
  gap: 8px;
  display: inline-flex;
}

.toggle__wrapper--bottom {
  padding-left: 16px;
  position: absolute;
  bottom: 18px;
}

.toggle__wrapper--right {
  display: none;
}

@media screen and (min-width: 768px) {
  .toggle__wrapper--right {
    display: inline-flex;
    position: absolute;
    top: 20px;
    right: 16px;
  }
}

.toggle {
  height: 20px;
  width: 40px;
  cursor: pointer;
  background-color: #fff;
  border: .6px solid #4b48db;
  border-radius: 100px;
  position: relative;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
}

.toggle.active {
  background-color: #4b48db;
  border-color: #fff;
}

.toggle:before {
  content: "";
  height: 16px;
  width: 16px;
  background: #4b48db;
  border-radius: 50%;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 50%;
  right: 22px;
  transform: translateY(-50%);
}

.toggle.active:before {
  background-color: #fff;
  right: calc(100% - 38px);
}

@media screen and (min-width: 1281px) {
  .icon-theme {
    display: none;
  }
}

.theme__text {
  display: none;
}

@media screen and (min-width: 1281px) {
  .theme__text {
    font-size: 18px;
    line-height: 1.3;
    display: inline;
  }
}

.theme__text--light {
  margin-right: 12px;
}

.theme__text--dark {
  margin-left: 12px;
}

.icon__sun {
  fill: #4440f6;
  color: #4440f6;
}

.icon__moon {
  fill: #a8a8a8;
  color: #a8a8a8;
}

.news-card__list {
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  display: flex;
}

.news-card__image-container {
  grid-template-columns: repeat(1, 1fr);
  gap: 28px;
  display: grid;
}

@media screen and (min-width: 768px) {
  .news-card__image-container {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}

@media screen and (min-width: 1281px) {
  .news-card__image-container {
    width: 100%;
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
  }
}

.news-card__item {
  height: 676px;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
