  overflow: hidden;
}

.news-card__title {
  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
  letter-spacing: -.01em;
  color: #111321;
  max-height: 132px;
  -webkit-box-orient: vertical;
  margin: 20px 0;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.375;
  display: -webkit-box;
  overflow: hidden;
}

.news-card__date {
  color: #a8a8a8;
  font-size: 16px;
  line-height: 1.375;
}

.news-card__image {
