  left: 0;
}

.news-card__favorite-button {
  color: #000;
  text-align: center;
  cursor: pointer;
  z-index: 2;
  background-color: #fff;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.333;
