}

.news-card__favorite-icon {
  fill: #4440f6;
  margin-left: 6px;
}

.news-card__overlay {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(244, 244, 244, .4);
  position: absolute;
  top: 0;
  right: 0;
}

.news-card__p {
  color: #00dd73;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.36;
  position: absolute;
  top: 20px;
  right: 43px;
}

.news-card__ok {
  fill: #00dd73;
  position: absolute;
  top: 24px;
  right: 22px;
}

.container .favorite {
  padding-top: 30px;
}

.accordion {
  width: 100%;
}

.read__date {
  letter-spacing: .02em;
  color: #111321;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
}

.accordion__line {
  width: 100%;
  border: 1px solid #929292;
  margin: 0;
}

.accordion__wrap {
  margin-top: 60px;
  position: relative;
}

.accordion__wrap:last-child {
  margin-bottom: 210px;
}

.accordion__active {
  justify-content: start;
  align-items: center;
  gap: 12px;
  display: flex;
}

.read__date {
  cursor: pointer;
  position: relative;
}

.accordion_icon {
  color: #111321;
  flex-shrink: 0;
  transition: transform .5ms;
  display: block;
  overflow: visible;
}

.active .accordion_icon {
  transform: rotate(180deg);
}

.content {
  height: 0;
  text-align: justify;
  width: 100%;
  transition: all .5s;
  position: relative;
  overflow: hidden;
}

.accordion__wrap.active .content {
  height: auto;
  overflow: visible;
}

.sub-menu {
