.categories__dropdown-button {
  text-align: center;
  height: 31px;
  color: #4440f6;
  background-color: #fff;
  border: 1px solid #4440f7;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 8px 21px;
  font-family: Manrope, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
